.alm-btn-wrap {
	text-align: center;
	margin-bottom: 12px;

    .alm-load-more-btn.more {
        width: 200px;
        display: inline-block;
        position: relative;

        &.loading {
            background-image: url(../img/loading.svg);
            background-repeat: no-repeat;
            background-position: center left;
            background-size: 40px;
        }

        &.done {
            // text-indent: -9999px;

