            //     content: "teste";
            //     position: absolute;
            //     text-indent: 0;
            //     left: 0;
            //     right: 0;
            // }
        }

        &:disabled {
            background-color: rgba($pink, 0.8);
            cursor: not-allowed;
        }
    }
}
